import { httpClient } from '@api/httpClient';

export type DeactivateDeviceBody = {
  userId: string;
  deviceId: string;
};

type DeactivateDeviceDto = {
  url: string;
  body: DeactivateDeviceBody;
};

type DeactivateDeviceResponse = {
  data: {
    success: boolean;
  };
  error: unknown;
};

export async function deactivateDevice({
  url,
  body,
}: DeactivateDeviceDto): Promise<DeactivateDeviceResponse> {
  return await httpClient
    .post<DeactivateDeviceResponse>(url, { json: body })
    .json();
}
