import { browser_api } from '@app/browser_api';
import type { Runtime } from 'webextension-polyfill';
import { appStore } from './index';
import { StoreMessageActions } from '../shared';
import {
  deleteLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import { isStoreMessageOfType, postMessageHandler } from '../utils';
import { LocalStorageData } from '@app/storage/local/types/sharedTypes';

const ports: Runtime.Port[] = [];

const handleMessage = async (msg: unknown) => {
  if (isStoreMessageOfType(msg, StoreMessageActions.SET)) {
    // TODO 502  deleteLocalStorageData  appStore.setState(...)    appStore.setState
    await setLocalStorageData(msg.payload);
    // appStore.setState((prev) => ({ ...prev, ...msg.payload }));
  }
  if (isStoreMessageOfType(msg, StoreMessageActions.REMOVE)) {
    const keys = msg.payload as Array<keyof LocalStorageData>;
    // TODO 502  deleteLocalStorageData  appStore.setState(...)    appStore.setState
    await deleteLocalStorageData(keys);
    // appStore.setState((prev) => {
    //   const newState = { ...prev };
    //   for (const key of keys) {
    //     delete newState[key];
    //   }
    //   return newState;
    // });
  }
};

export const onConnectListener = () => {
  browser_api.runtime.onConnect.addListener((port) => {
    console.warn('runtime.onConnect: ', { ports, port });
    ports.push(port);

    const state = appStore.getState();
    postMessageHandler(port, StoreMessageActions.STATE, state);

    port.onMessage.addListener(handleMessage);

    port.onDisconnect.addListener(() => {
      port.onMessage.removeListener(handleMessage);

      const idx = ports.indexOf(port);
      if (idx !== -1) ports.splice(idx, 1);
    });
  });

  // :    
  appStore.subscribe(async (state) => {
    // TODO 502  setLocalStorageData,    appStore.setState.      storage.local.onChanged,   appStore.setState.
    // await setLocalStorageData(state);
    ports.forEach((port) => {
      postMessageHandler(port, StoreMessageActions.STATE, state);
    });
  });
};
