import {
  YOUTUBE_API_STATS_URL,
  YOUTUBE_STATUS_CHECK_URL,
  YOUTUBE_YOUTUBEI_URL,
  GOOGLE_VIDEOPLAYBACK_URL,
} from './constants';

export function isErrUrl(errorUrl: string): boolean {
  const URLS = [
    YOUTUBE_STATUS_CHECK_URL,
    YOUTUBE_YOUTUBEI_URL,
    YOUTUBE_API_STATS_URL,
    GOOGLE_VIDEOPLAYBACK_URL,
  ];

  return URLS.some((url) => errorUrl.includes(url));
}
