import { browser_api } from '@app/browser_api';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { backgroundScope } from '@background/shared';
import type { WebRequest } from 'webextension-polyfill';
import { Alarms } from '../../app/alarms/shared';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '../../app/storage/local/shared';

import { isTinyImgError } from './isTinyImgError';
import { isErrUrl, isTargetWebRequestError } from './isTargetWebRequestError';
import { messageConnectionRetryToAllYoutubeTabs } from './messageConnectionRetryToAllYoutubeTabs';
import { reloadAllYoutubeTabs } from './reloadAllYoutubeTabs';
import { messageConnectionFailToAllTabs } from './messageConnectionFailToAllTabs';
import { messageConnectionSuccessToAllTabs } from './messageConnectionSuccessToAllTabs';

export const WEB_REQUEST_URLS = process.env.WEB_REQUEST_URLS?.split(',') || [];

export const networkConnectionMonitorListener = createWebRequestErrorHandler();

export function initNetworkConnectionMonitor(): void {
  // Listener for network errors specifically targeting YouTube
  if (
    browser_api.webRequest.onErrorOccurred.hasListener(
      networkConnectionMonitorListener,
    )
  ) {
    browser_api.webRequest.onErrorOccurred.removeListener(
      networkConnectionMonitorListener,
    );
  }

  browser_api.webRequest.onErrorOccurred.addListener(
    networkConnectionMonitorListener,
    {
      urls: WEB_REQUEST_URLS,
    },
  );

  // Listener for successful requests to YouTube
  if (
    browser_api.webRequest.onCompleted.hasListener(handleWebRequestComplete)
  ) {
    browser_api.webRequest.onCompleted.removeListener(handleWebRequestComplete);
  }

  browser_api.webRequest.onCompleted.addListener(handleWebRequestComplete, {
    urls: WEB_REQUEST_URLS,
  });
}

export async function handleWebRequestComplete(
  details: WebRequest.OnCompletedDetailsType,
): Promise<void> {
  const { isNetworkError } = await getLocalStorageData(
    LocalStorageKey.IsNetworkError,
  );

  if (isErrUrl(details.url) && isNetworkError) {
    await setLocalStorageData({
      isNetworkError: false,
    });
    await messageConnectionSuccessToAllTabs();
  }
}

export function createWebRequestErrorHandler() {
  return async (details: WebRequest.OnErrorOccurredDetailsType) => {
    const { currentState } = await getLocalStorageData(
      LocalStorageKey.CurrentState,
    );

    if (currentState !== 'connected') {
      return;
    }

    backgroundScope.setTag('WebRequestError', details.error);
    backgroundScope.setExtras({
      statusCode: details.statusCode,
      statusLine: details.statusLine,
      type: details.type,
      url: details.url,
      error: details.error,
      initiator: details.initiator,
    });

    const isTinyimgError = isTinyImgError(details);

    backgroundScope.captureException(new Error(details.error));

    if (isTinyimgError) {
      console.warn('--------------------------------');
      console.warn('The tiny img error occurred', isTinyimgError);
      console.warn('--------------------------------');

      const { isLitePAC } = await getLocalStorageData(
        LocalStorageKey.IsLitePAC,
      );

      if (!isLitePAC) {
        return;
      }

      const isAlarmExists = await browser_api.alarms.get(
        Alarms.UpdatePacScriptToFullPAC,
      );

      if (isAlarmExists) {
        await browser_api.alarms.clear(Alarms.UpdatePacScriptToFullPAC);
      }

      browser_api.alarms.create(Alarms.UpdatePacScriptToFullPAC, {
        delayInMinutes: 0,
      });
    }

    const {
      isNetworkError,
      isNetworkErrorHandlingInProgress,
      isNetworkErrorHandlingRetriesFailed,
    } = await getLocalStorageData([
      LocalStorageKey.IsNetworkError,
      LocalStorageKey.CurrentState,
      LocalStorageKey.isNetworkErrorHandlingInProgress,
      LocalStorageKey.isNetworkErrorHandlingRetriesFailed,
    ]);

    const isTargetRequest = isTargetWebRequestError(details.error, details.url);

    if (isTargetRequest) {
      console.warn('--------------------------------');
      console.warn('The network error occurred', isTargetRequest);
      console.warn('--------------------------------');

      if (!isNetworkError) {
        await setLocalStorageData({ isNetworkError: true });

        if (!isNetworkErrorHandlingInProgress && currentState === 'connected') {
          const alarmEnableProxyOnFail = await browser_api.alarms.get(
            Alarms.EnableProxyOnFail,
          );

          if (alarmEnableProxyOnFail) {
            await browser_api.alarms.clear(Alarms.EnableProxyOnFail);
          }

          console.warn('--------------------------------');
          console.warn('The new EnableProxyOnFail alarm created');
          console.warn('--------------------------------');

          browser_api.alarms.create(Alarms.EnableProxyOnFail, {
            delayInMinutes: 0,
          });
          await setLocalStorageData({
            [LocalStorageKey.isNetworkErrorHandlingInProgress]: true,
          });
        }
      }

      if (isNetworkErrorHandlingInProgress) {
        await messageConnectionRetryToAllYoutubeTabs();
        await reloadAllYoutubeTabs();
      }

      if (isNetworkErrorHandlingRetriesFailed) {
        await messageConnectionFailToAllTabs();
      }
    }
  };
}
