import { Actions } from '@app/actions/shared';
import { Alarms } from '@app/alarms/shared';
import { browser_api } from '@app/browser_api';
import type {
  ShowSubscriptionExpiringModalMessage,
  SubscriptionExpiringModalStatus,
} from '@app/messages/shared';

async function sendSubscriptionExpiringMessage(
  status: SubscriptionExpiringModalStatus,
): Promise<void> {
  try {
    const tabs = await browser_api.tabs.query({
      url: ['*://*.youtube.com/*'],
    });

    await Promise.all(
      tabs.map((tab) => {
        if (tab.id) {
          return browser_api.tabs.sendMessage<ShowSubscriptionExpiringModalMessage>(
            tab.id,
            {
              action: Actions.ShowSubscriptionExpiringModal,
              payload: { status },
            },
          );
        }
        return null;
      }),
    );
  } catch (error) {
    console.warn('Error handling subscription expiring alarm:', error);
  }
}

async function subscriptionExpiringAlarmHandlerListener(
  alarm: browser_api.Alarms.Alarm,
): Promise<void> {
  const name = alarm.name as Alarms;

  if (name === Alarms.SubscriptionExpiredAlert) {
    sendSubscriptionExpiringMessage('expired');
  }
  if (name === Alarms.SubscriptionExpiringAlert) {
    sendSubscriptionExpiringMessage('expiring');
  }
}

export function setupSubscriptionExpiringAlarmHandler(): void {
  if (
    !browser_api.alarms.onAlarm.hasListener(
      subscriptionExpiringAlarmHandlerListener,
    )
  ) {
    browser_api.alarms.onAlarm.addListener(
      subscriptionExpiringAlarmHandlerListener,
    );
  }
}

export function removeSubscriptionExpiringAlarmHandler(): void {
  if (
    browser_api.alarms.onAlarm.hasListener(
      subscriptionExpiringAlarmHandlerListener,
    )
  ) {
    browser_api.alarms.onAlarm.removeListener(
      subscriptionExpiringAlarmHandlerListener,
    );
  }
}
