import { browser_api } from '@app/browser_api';

export async function reloadAllYoutubeTabs(): Promise<void> {
  const tabs = await browser_api.tabs.query({
    url: '*://*.youtube.com/*',
  });

  await Promise.all(
    tabs.map((tab) => {
      if (tab.id) {
        console.warn('reloading tab', tab.id);
        return browser_api.tabs.reload(tab.id);
      }
      return null;
    }),
  );
}
