import { ProxyAuthListenerType, ProxyCredential } from './types/ProxyData';

export function createProxyAuthListener(proxyCredential: ProxyCredential) {
  return function proxyAuthListener(
    details: Parameters<ProxyAuthListenerType>[0],
  ): ReturnType<ProxyAuthListenerType> {
    if (!details.isProxy || !proxyCredential) return;

    const { challenger } = details;
    const { host, port } = challenger;

    const credentials = proxyCredential[`${host}:${port}`];

    if (credentials?.userName && credentials?.password) {
      const { userName, password } = credentials;

      return {
        authCredentials: {
          username: userName,
          password: password,
        },
      };
    } else {
      console.error(`No credentials found for proxy ${host}:${port}`);
    }
  };
}
