import { CookieKey, getCookieData } from '@app/storage/cookies/shared';
import { setLocalStorageData } from '@app/storage/local/shared';
import { OnboardingStep } from '@popup/project/youboost-neo/components/Onboarding/ui/constants';

import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { getMetaApiData } from '@utils/metaApiData';

export async function moveUserIdAndDeviceIdFromCookiesToLocalStorage() {
  const { cookieDomain } = await getMetaApiData();
  const { userId } = await getCookieData(CookieKey.UserId, cookieDomain);
  const { deviceId } = await getCookieData(CookieKey.DeviceId, cookieDomain);

  await setLocalStorageData({
    [LocalStorageKey.CookieLoaded]: false,
  });

  if (userId) {
    await setLocalStorageData({
      [LocalStorageKey.UserId]: userId,
      [LocalStorageKey.OnboardingScreenStatus]: OnboardingStep.Done,
    });
  }

  if (deviceId) {
    await setLocalStorageData({
      [LocalStorageKey.DeviceId]: deviceId,
    });
  }

  await setLocalStorageData({
    [LocalStorageKey.CookieLoaded]: true,
  });
}
