import { checkProxyAvailability } from '@api/checkProxyAvailability';
import { fetchProxyData } from '@api/fetchProxyData';
import { Alarms } from '@app/alarms/shared';
import { browser_api } from '@app/browser_api';
import { Feature, isFeatureStore } from '@app/features/types/Feature';
import { getPACData } from '@app/storage/local/getPACData';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import {
  PACData,
  PACPremiumData,
} from '@app/storage/local/types/persistentTypes';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { getDeviceIP } from '@app/storage/utils/getDeviceIP';
import { getDeviceId } from '@app/storage/utils/getDeviceId';
import { backgroundScope } from '@background/shared';
import { getSubscriptionInfo } from '@utils/getSubscriptionInfo';
import { getMetaApiData } from '@utils/metaApiData';
import { initUpdateProxyAuth } from './auth';
import { checkProxyError } from './checkProxyError';
import { getProxyCredentials } from './getProxyCredentials';
import { clearProxy, setProxy } from './proxy';
import {
  BaseProxyDataResponse,
  PremiumProxies,
  PremiumProxyDataResponse,
  ProxyFailReason,
} from './types/ProxyData';

const ENABLE_PROXY_ON_FAIL_PERIOD_IN_MINITES = Number(
  process.env.ENABLE_PROXY_ON_FAIL_PERIOD_IN_MINUTES || 5,
);
const ENABLE_PROXY_ON_FAIL_MAX_RETRY_ATTEMPTS = Number(
  process.env.ENABLE_PROXY_ON_FAIL_MAX_RETRY_ATTEMPTS || 3,
);

const ENABLE_PROXY_ON_FAIL_ERROR_MESSAGE =
  '   .  ';

class ProxyController {
  private _fetchProxyData = fetchProxyData;

  set fetchProxyData(value: typeof fetchProxyData) {
    this._fetchProxyData = value;
  }

  get fetchProxyData(): typeof fetchProxyData {
    return this._fetchProxyData;
  }
}

export const proxyController = new ProxyController();

export async function initProxy(): Promise<void> {
  const { targetState } = await getLocalStorageData([
    LocalStorageKey.TargetState,
  ]);

  if (targetState === undefined) {
    await setLocalStorageData({ targetState: 'connected' });
  } else {
    await toggleProxy();
  }
}

async function toggleProxy(): Promise<void> {
  const { targetState, currentState } = await getLocalStorageData([
    LocalStorageKey.TargetState,
    LocalStorageKey.CurrentState,
  ]);

  if (targetState === 'connected') {
    await enableProxy();
  } else if (
    targetState === 'disconnected' &&
    currentState !== 'disconnected'
  ) {
    disableProxy();
  }
}

function isPACPremiumData(
  data: PACData | PACPremiumData,
): data is PACPremiumData {
  if (process.env.EXTENSION_CODE === 'vrubel') {
    return (data as PACPremiumData).premium !== undefined;
  }
  return (
    (data as PACPremiumData).premium !== undefined &&
    (data as PACPremiumData).allInternet !== undefined
  );
}

function isPACData(data: PACData | PACPremiumData): data is PACData {
  return (data as PACData).free !== undefined;
}

async function updatePacScriptToFullPAC(): Promise<void> {
  await setLocalStorageData({ [LocalStorageKey.IsLitePAC]: false });

  await getPACData({ shouldRefetch: true });
  await enableProxy();

  browser_api.alarms.create(Alarms.RestoreLitePAC, {
    delayInMinutes: process.env.RESTORE_LITE_PAC_DELAY_MINUTES
      ? parseInt(process.env.RESTORE_LITE_PAC_DELAY_MINUTES)
      : 24 * 60, // 24 hours
  });
}

async function updatePacScriptToLitePAC(): Promise<void> {
  await setLocalStorageData({ [LocalStorageKey.IsLitePAC]: true });

  await getPACData({
    shouldRefetch: true,
  });
  await enableProxy();
}

async function enableProxy({
  onFail,
  onFailReason,
}: { onFail?: boolean; onFailReason?: ProxyFailReason } = {}): Promise<void> {
  try {
    await setLocalStorageData({ currentState: 'connecting' });

    await getSubscriptionInfo();

    const [{ apiBaseUrl }, deviceId, deviceIp] = await Promise.all([
      getMetaApiData(),
      getDeviceId(),
      getDeviceIP(),
    ]);

    const proxyData = await proxyController.fetchProxyData({
      apiBaseUrl,
      deviceId,
      deviceIp,
      onFail,
      onFailReason,
    });

    if (proxyData) {
      const proxyCredential = await getProxyCredentials(proxyData);

      initUpdateProxyAuth(proxyCredential);
    }

    await checkProxyError(proxyData);

    await setLocalStorageData({
      [LocalStorageKey.ProxyData]: proxyData,
    });

    const pacData = await getPACData({
      shouldRefetch: false,
    });

    if (!pacData) {
      await setLocalStorageData({ currentState: 'error' });
      throw new Error('PAC data is missing');
    }

    const { userId, hasPremium, isAllInternetProxy } =
      await getLocalStorageData([
        LocalStorageKey.UserId,
        LocalStorageKey.HasPremium,
        LocalStorageKey.IsAllInternetProxy,
      ]);

    let pacString = null;
    let proxyPort = null;
    let proxyHost = null;
    let vpnProxyHost = null;
    let vpnProxyPort = null;

    if (isPACPremiumData(pacData) && userId && hasPremium) {
      const { proxies } = proxyData as PremiumProxyDataResponse;

      const [premiumProxy, allInternetProxy] = proxies?.reduce<PremiumProxies>(
        (acc, proxy) => {
          if (proxy.proxy_type === 'default') {
            acc[0] = proxy;
          } else if (proxy.proxy_type === 'vpn') {
            acc[1] = proxy;
          }
          return acc;
        },
        [],
      );

      pacString = pacData.premium;
      proxyPort = premiumProxy.port;
      proxyHost = premiumProxy.host;

      if (isAllInternetProxy) {
        pacString = pacData.allInternet;
        vpnProxyHost = allInternetProxy.host;
        vpnProxyPort = allInternetProxy.port;
      }
    } else if (isPACData(pacData)) {
      if (!(proxyData as BaseProxyDataResponse).data.proxy_server) {
        throw new Error('Proxy data is missing');
      }

      pacString = pacData.free;
      proxyPort = (proxyData as BaseProxyDataResponse).data.proxy_server.port;
      proxyHost = (proxyData as BaseProxyDataResponse).data.proxy_server.host;
    } else {
      await setLocalStorageData({ currentState: 'error' });
      throw new Error('Invalid PAC data format');
    }

    await setProxy({
      port: proxyPort!,
      host: proxyHost!,
      vpnHost: vpnProxyHost,
      vpnPort: vpnProxyPort,
      pacString,
      shouldDecompress: hasPremium,
    });

    await setLocalStorageData({
      [LocalStorageKey.isNetworkErrorHandlingRetriesFailed]: false,
    });

    const alarmEnableProxyOnFail = await browser_api.alarms.get(
      Alarms.EnableProxyOnFail,
    );

    if (alarmEnableProxyOnFail) {
      await browser_api.alarms.clear(Alarms.EnableProxyOnFail);
    }

    browser_api.alarms.create(Alarms.EnableProxyOnFail, {
      delayInMinutes: 0,
      periodInMinutes: ENABLE_PROXY_ON_FAIL_PERIOD_IN_MINITES,
    });
  } catch (error) {
    console.error('Error loading configuration:', error);
    await setLocalStorageData({ currentState: 'error' });
  }
}

function disableProxy(): void {
  clearProxy();
}

browser_api.storage.onChanged.addListener(
  async (changes: {
    // TODO rewrite types
    targetState?: { newValue: string };
    isAllInternetProxy?: { newValue: boolean };
  }) => {
    if (changes.targetState) {
      await toggleProxy();
    }

    if (changes.isAllInternetProxy) {
      const { hasPremium } = await getLocalStorageData([
        LocalStorageKey.HasPremium,
      ]);

      if (hasPremium) {
        await toggleProxy();
      }
    }
  },
);

browser_api.storage.onChanged.addListener(async (changes) => {
  const featureStoreChange = changes.featureStore;

  if (
    !featureStoreChange ||
    !isFeatureStore(featureStoreChange.newValue) ||
    !isFeatureStore(featureStoreChange.oldValue)
  )
    return;

  const newFeatures = featureStoreChange.newValue.featuresList;
  const oldFeatures = featureStoreChange.oldValue.featuresList;

  if (newFeatures[Feature.PACLite] !== oldFeatures[Feature.PACLite]) {
    const isLite = newFeatures[Feature.PACLite];

    await setLocalStorageData({ [LocalStorageKey.IsLitePAC]: isLite });

    await getPACData({
      shouldRefetch: true,
    });

    backgroundScope.setExtras({
      label: Feature.PACLite,
      isLite,
      newValue: newFeatures,
      oldValue: oldFeatures,
    });

    backgroundScope.captureMessage(
      `Feature ${Feature.PACLite} changed to ${isLite}`,
    );
  }
});

browser_api.alarms.onAlarm.addListener(async (alarm) => {
  const name = alarm.name as Alarms;

  if (name === Alarms.EnableProxy) {
    await enableProxy();
  }

  if (name === Alarms.DisableProxy) {
    disableProxy();
  }

  if (name === Alarms.EnableProxyOnFail) {
    let { enableProxyOnFailMaxRetryAttempts, currentState } =
      await getLocalStorageData([
        LocalStorageKey.EnableProxyOnFailMaxRetryAttempts,
        LocalStorageKey.CurrentState,
      ]);

    if (currentState !== 'connected') {
      return;
    }

    console.warn('--------------------------------');
    console.warn('The EnableProxyOnFail alarm occurred');
    console.warn('--------------------------------');

    await setLocalStorageData({
      [LocalStorageKey.isNetworkErrorHandlingInProgress]: true,
    });

    if (enableProxyOnFailMaxRetryAttempts === undefined) {
      enableProxyOnFailMaxRetryAttempts =
        ENABLE_PROXY_ON_FAIL_MAX_RETRY_ATTEMPTS;
    }

    const { isProxyAvailable, onFailReason } = await checkProxyAvailability();

    if (isProxyAvailable) {
      await setLocalStorageData({
        [LocalStorageKey.EnableProxyOnFailMaxRetryAttempts]:
          // TODO replace constant string with i18n translation in task FRO-630 https://vpnn.youtrack.cloud/issue/FRO-630/extensions-dobavit-i18n-v-bekgraund
          ENABLE_PROXY_ON_FAIL_MAX_RETRY_ATTEMPTS,
        [LocalStorageKey.isNetworkErrorHandlingInProgress]: false,
      });
    } else if (enableProxyOnFailMaxRetryAttempts > 0) {
      console.warn('--------------------------------');
      console.warn(
        'Trying to obtain another Proxy',
        enableProxyOnFailMaxRetryAttempts,
      );
      console.warn('--------------------------------');

      await setLocalStorageData({
        [LocalStorageKey.EnableProxyOnFailMaxRetryAttempts]:
          enableProxyOnFailMaxRetryAttempts - 1,
        [LocalStorageKey.isNetworkErrorHandlingRetriesFailed]: false,
      });

      await enableProxy({ onFail: true, onFailReason: onFailReason });
    } else {
      await setLocalStorageData({
        [LocalStorageKey.ErrorMessage]: ENABLE_PROXY_ON_FAIL_ERROR_MESSAGE,
        [LocalStorageKey.EnableProxyOnFailMaxRetryAttempts]:
          ENABLE_PROXY_ON_FAIL_MAX_RETRY_ATTEMPTS,
        [LocalStorageKey.CurrentState]: 'error',
        [LocalStorageKey.isNetworkErrorHandlingInProgress]: false,
        [LocalStorageKey.isNetworkErrorHandlingRetriesFailed]: true,
      });
    }
  }

  if (name === Alarms.RestoreLitePAC) {
    console.warn('--------------------------------');
    console.warn('The RestoreLitePAC alarm occurred');
    console.warn('--------------------------------');
    await updatePacScriptToLitePAC();
  }

  if (name === Alarms.UpdatePacScriptToFullPAC) {
    console.warn('--------------------------------');
    console.warn('The UpdatePacScriptToFullPAC alarm occurred');
    console.warn('--------------------------------');
    await updatePacScriptToFullPAC();
  }
});
