import {
  YOUTUBE_STATUS_CHECK_URL,
  YOUTUBE_YOUTUBEI_URL,
  YOUTUBE_API_STATS_URL,
  GOOGLE_VIDEOPLAYBACK_URL,
} from './constants';

function isErrProxyConnectionFailed(error: string, errorUrl: string): boolean {
  const ERR_PROXY_CONNECTION_FAILED = 'net::ERR_PROXY_CONNECTION_FAILED';

  const URLS = [
    YOUTUBE_STATUS_CHECK_URL,
    YOUTUBE_YOUTUBEI_URL,
    YOUTUBE_API_STATS_URL,
  ];

  const isErrProxyConnectionFailed =
    error === ERR_PROXY_CONNECTION_FAILED &&
    URLS.some((url) => errorUrl.includes(url));

  return isErrProxyConnectionFailed;
}

function isErrConnectionReset(error: string, errorUrl: string): boolean {
  const ERR_CONNECTION_RESET = 'net::ERR_CONNECTION_RESET';
  const URLS = [YOUTUBE_YOUTUBEI_URL, GOOGLE_VIDEOPLAYBACK_URL];

  const isErrConnectionReset =
    error === ERR_CONNECTION_RESET &&
    URLS.some((url) => errorUrl.includes(url));

  return isErrConnectionReset;
}

function isErrTimedOut(error: string, errorUrl: string): boolean {
  const ERR_TIMED_OUT = 'net::ERR_TIMED_OUT';
  const URLS = [YOUTUBE_YOUTUBEI_URL, GOOGLE_VIDEOPLAYBACK_URL];

  const isErrTimedOut =
    error === ERR_TIMED_OUT && URLS.some((url) => errorUrl.includes(url));

  return isErrTimedOut;
}

function isErrConnectionClosed(error: string, errorUrl: string): boolean {
  const ERR_CONNECTION_CLOSED = 'net::ERR_CONNECTION_CLOSED';
  const URLS = [YOUTUBE_YOUTUBEI_URL];

  const isErrConnectionClosed =
    error === ERR_CONNECTION_CLOSED &&
    URLS.some((url) => errorUrl.includes(url));

  return isErrConnectionClosed;
}

export function isTargetWebRequestError(error: string, url: string): boolean {
  return (
    isErrProxyConnectionFailed(error, url) ||
    isErrConnectionReset(error, url) ||
    isErrTimedOut(error, url) ||
    isErrConnectionClosed(error, url)
  );
}


