import { Alarms } from '@app/alarms/shared';
import { browser_api } from '@app/browser_api';
import { getLocalStorageData } from '@app/storage/local/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { getMetaApiData } from '@utils/metaApiData';

const MINUTE_IN_MS = 60 * 1000;

export async function initSubscriptionExpiringAlarms(): Promise<void> {
  try {
    await Promise.all([
      browser_api.alarms.clear(Alarms.SubscriptionExpiredAlert),
      browser_api.alarms.clear(Alarms.SubscriptionExpiringAlert),
    ]);

    const { paidUntil } = await getLocalStorageData(LocalStorageKey.PaidUntil);
    const { subscriptionExpiringAlerts } = await getMetaApiData();
    if (!subscriptionExpiringAlerts || !paidUntil) {
      return;
    }

    const paidUntilTimestamp = Date.parse(paidUntil.replace('\n', ''));
    const now = Date.now();

    const timeToExpire = paidUntilTimestamp - now;
    const minutesToExpire = timeToExpire / MINUTE_IN_MS;

    const firstAlertTimingInMinutes =
      subscriptionExpiringAlerts.firstTimingInMinutes;
    const secondAlertTimingInMinutes =
      subscriptionExpiringAlerts.secondTimingInMinutes;
    const delayedAlertTimingInMinutes =
      subscriptionExpiringAlerts.delayedTimingInMinutes;

    //    
    if (
      firstAlertTimingInMinutes &&
      minutesToExpire > firstAlertTimingInMinutes
    ) {
      browser_api.alarms.create(Alarms.SubscriptionExpiringAlert, {
        delayInMinutes: minutesToExpire - firstAlertTimingInMinutes,
      });
      return;
    }

    //    
    if (
      secondAlertTimingInMinutes &&
      minutesToExpire > secondAlertTimingInMinutes
    ) {
      browser_api.alarms.create(Alarms.SubscriptionExpiringAlert, {
        delayInMinutes: minutesToExpire - secondAlertTimingInMinutes,
      });
      return;
    }

    //    
    if (minutesToExpire > 0) {
      browser_api.alarms.create(Alarms.SubscriptionExpiredAlert, {
        delayInMinutes: minutesToExpire,
      });
      return;
    }

    //       n 
    if (
      delayedAlertTimingInMinutes &&
      -minutesToExpire < delayedAlertTimingInMinutes
    ) {
      browser_api.alarms.create(Alarms.SubscriptionExpiredAlert, {
        delayInMinutes: delayedAlertTimingInMinutes,
      });
    }
    return;
  } catch (error) {
    console.warn('Error initializing subscription expiring alarms:', error);
  }
}
