import { Actions } from '@app/actions/shared';
import { browser_api } from '@app/browser_api';
import { featureStore } from '@app/features/list';
import { Feature } from '@app/features/types/Feature';
import { isMessageOfType } from '@app/messages/shared';
import {
  initSubscriptionExpiringAlarms,
  setupSubscriptionExpiringAlarmHandler,
  removeSubscriptionExpiringAlarmHandler,
} from './alarms';
import { removeStorageListener, setupStorageListener } from './storageListener';
import {
  removeSubscriptionExpiringModalMessageHandler,
  setupSubscriptionExpiringModalMessageHandler,
} from './messageListeners';

async function initSubscriptionExpiringOnYoutubeBackgroundListener(
  message: unknown,
): Promise<void> {
  if (isMessageOfType(message, Actions.InitSubscriptionExpiringScriptReady)) {
    const { featuresList } = await featureStore.getSnapshotAsync();

    if (featuresList?.[Feature.SubscriptionExpiringAlerts]) {
      setupSubscriptionExpiringAlarmHandler();
      setupSubscriptionExpiringModalMessageHandler();
      initSubscriptionExpiringAlarms();
      setupStorageListener();
    } else {
      removeSubscriptionExpiringAlarmHandler();
      removeSubscriptionExpiringModalMessageHandler();
      removeStorageListener();
    }
  }
}

export function initSubscriptionExpiringOnYoutubeBackground(): void {
  if (
    !browser_api.runtime.onMessage.hasListener(
      initSubscriptionExpiringOnYoutubeBackgroundListener,
    )
  ) {
    browser_api.runtime.onMessage.addListener(
      initSubscriptionExpiringOnYoutubeBackgroundListener,
    );
  }
}
