import {
  type ProxyAuthListenerType,
  type ProxyCredential,
} from './types/ProxyData';
import { browser_api } from '@app/browser_api';
import { createProxyAuthListener } from './createProxyAuthListener';

// // Important to store the listener in a out of scope variable to reinitialize it
export let prevListener: ProxyAuthListenerType;

export function initUpdateProxyAuth(proxyCredential: ProxyCredential) {
  console.warn('Proxy auth init/update started');

  if (browser_api.webRequest.onAuthRequired.hasListener(prevListener)) {
    console.warn('Removing old listener');
    browser_api.webRequest.onAuthRequired.removeListener(prevListener);
  }

  const proxyAuthListener = createProxyAuthListener(proxyCredential);

  prevListener = proxyAuthListener;

  browser_api.webRequest.onAuthRequired.addListener(
    prevListener,
    { urls: ['<all_urls>'] },
    ['blocking'],
  );

  console.warn('Proxy auth init/update finished');
}
