import { browser_api } from '@app/browser_api';
import { initSubscriptionExpiringAlarms } from '../alarms';

async function storagePaidUntilListener(changes: {
  paidUntil?: { newValue: string };
}): Promise<void> {
  if (changes.paidUntil) {
    initSubscriptionExpiringAlarms();
  }
}

export function setupStorageListener(): void {
  if (!browser_api.storage.onChanged.hasListener(storagePaidUntilListener)) {
    browser_api.storage.onChanged.addListener(storagePaidUntilListener);
  }
}

export function removeStorageListener(): void {
  if (browser_api.storage.onChanged.hasListener(storagePaidUntilListener)) {
    browser_api.storage.onChanged.removeListener(storagePaidUntilListener);
  }
}
