import { Actions } from '@app/actions/shared';
import { browser_api } from '@app/browser_api';
import {
  isMessageOfType,
  ShowSubscriptionExpiringScreenMessage,
} from '@app/messages/shared';
import { initSubscriptionExpiringAlarms } from '../alarms';

async function openPopup(): Promise<void> {
  try {
    if ('openPopup' in browser_api.action) {
      const window = await browser_api.windows.getCurrent();
      if (window.id) {
        await browser_api.windows.update(window.id, { focused: true });
      }
      await browser_api.action.openPopup({ windowId: window.id });
    }
  } catch (err) {
    console.warn('Error opening popup:', err);
  }
}

async function temporaryPopupListenerForBuySubscription(
  message: unknown,
): Promise<void> {
  if (isMessageOfType(message, Actions.PopupReady)) {
    await browser_api.runtime.sendMessage<ShowSubscriptionExpiringScreenMessage>(
      {
        action: Actions.ShowSubscriptionExpiringScreen,
        payload: { status: 'buy-subscription' },
      },
    );

    browser_api.runtime.onMessage.removeListener(
      temporaryPopupListenerForBuySubscription,
    );
  }
}

async function temporaryPopupListenerForGoToFree(
  message: unknown,
): Promise<void> {
  if (isMessageOfType(message, Actions.PopupReady)) {
    await browser_api.runtime.sendMessage<ShowSubscriptionExpiringScreenMessage>(
      {
        action: Actions.ShowSubscriptionExpiringScreen,
        payload: { status: 'go-to-free' },
      },
    );

    browser_api.runtime.onMessage.removeListener(
      temporaryPopupListenerForGoToFree,
    );
  }
}

async function setupSubscriptionExpiringModalMessageHandlerListener(
  message: unknown,
): Promise<void> {
  if (isMessageOfType(message, Actions.SubscriptionExpiringModalClicked)) {
    const status = message.payload.status;
    try {
      if (status !== 'close') {
        try {
          if (status === 'buy-subscription') {
            browser_api.runtime.onMessage.addListener(
              temporaryPopupListenerForBuySubscription,
            );
          } else {
            browser_api.runtime.onMessage.addListener(
              temporaryPopupListenerForGoToFree,
            );
          }
        } catch (error) {
          console.error('Error navigating for subscription expiring:', error);
        } finally {
          await openPopup();
        }
      }

      const youtubeTabs = await browser_api.tabs.query({
        url: ['*://*.youtube.com/*'],
      });
      await Promise.all(
        youtubeTabs.map((tab) => {
          if (!tab.id) {
            return;
          }
          return browser_api.tabs.sendMessage(tab.id!, {
            action: Actions.HideSubscriptionExpiringModal,
          });
        }),
      );
    } catch (error) {
      console.warn(
        'Error handling subscription expiring modal clicked:',
        error,
      );
    } finally {
      await initSubscriptionExpiringAlarms();
    }
  }
}

export function setupSubscriptionExpiringModalMessageHandler(): void {
  if (
    !browser_api.runtime.onMessage.hasListener(
      setupSubscriptionExpiringModalMessageHandlerListener,
    )
  ) {
    browser_api.runtime.onMessage.addListener(
      setupSubscriptionExpiringModalMessageHandlerListener,
    );
  }
}

export function removeSubscriptionExpiringModalMessageHandler(): void {
  if (
    browser_api.runtime.onMessage.hasListener(
      setupSubscriptionExpiringModalMessageHandlerListener,
    )
  ) {
    browser_api.runtime.onMessage.removeListener(
      setupSubscriptionExpiringModalMessageHandlerListener,
    );
  }
}
